<template>
	<view class="search-box">
		<!-- 使用 uni-ui 提供的搜索组件 -->
		<uni-search-bar @input="input" :radius="100" cancelButton="none"></uni-search-bar>
		<!-- 搜索历史 -->
		<view class="history-box">
		  <!-- 标题区域 -->
		  <view class="history-title">
		    <text>搜索历史</text>
		    <uni-icons type="trash" size="17" @click="cleanHistory"></uni-icons>
		  </view>
		  <!-- 列表区域 -->
		  <view class="history-list">
		    <uni-tag :text="item" v-for="(item, i) in historys" :key="i"></uni-tag>
		  </view>
		</view>
		<view class="sugg-list">
		  <view class="sugg-item" v-for="(item, i) in searchResults" :key="i" @click="gotoDetail(item.goods_id)">
		    <view class="goods-name">{{item.goods_name}}</view>
		    <uni-icons type="arrowright" size="16"></uni-icons>
		  </view>
		</view>
	</view>
</template>

<script>
	import {
		requset
	} from "@/utils/api.js";
	export default {
		data() {
			return {
				show: true,
				showSync: true,
				searchVal: "",
				timer: null,
				// 搜索关键词
				kw: '',
				searchResults: [],
				 historyList: [],	
			};
		},
		onLoad() {
			this.historyList = JSON.parse(uni.getStorageSync('kw') || '[]')
		},
		computed: {
		  historys() {
		    // 注意：由于数组是引用类型，所以不要直接基于原数组调用 reverse 方法，以免修改原数组中元素的顺序
		    // 而是应该新建一个内存无关的数组，再进行 reverse 反转
		    return [...this.historyList].reverse()
		  }
		},
		methods: {
			input(e) {
				console.log('----',e)
				// 清除 timer 对应的延时器
				clearTimeout(this.timer)
				// 重新启动一个延时器，并把 timerId 赋值给 this.timer
				this.timer = setTimeout(() => {
				  this.kw = e
				  // 根据关键词，查询搜索建议列表
				  this.getSearchList()
				}, 500)
			},
			async getSearchList() {
			  // 判断关键词是否为空
			  if (this.kw === '') {
			    this.searchResults = []
			    return
			  }
			   
			  // 发起请求，获取搜索建议列表
			  const res = await requset({url:'/api/public/v1/goods/qsearch', data:{ query: this.kw }})
			  if (res.meta.status !== 200) return uni.showToast({
			  	 title: 请求超时,
				 icon:"error",
				 duration:1200
			  })
			  if (!res.message.length > 1) return uni.showToast({
			  	 title: 暂无搜索项,
			  				 icon:"none",
			  				 duration:1200
			  })
			  this.searchResults = res.message
			  this.saveSearchHistory()
			},
			gotoDetail(goods_id) {
			  uni.navigateTo({
			    // 指定详情页面的 URL 地址，并传递 goods_id 参数
			    url: '/subpage/goods_detail/goods_detail?goods_id=' + goods_id
			  })
			},
			saveSearchHistory() {
			    // 2.1 直接把搜索关键词 push 到 historyList 数组中
			     const set = new Set(this.historyList)
			      // 2. 调用 Set 对象的 delete 方法，移除对应的元素
			      set.delete(this.kw)
			      // 3. 调用 Set 对象的 add 方法，向 Set 中添加元素
			      set.add(this.kw)
			      // 4. 将 Set 对象转化为 Array 数组
			      this.historyList = Array.from(set)
				    uni.setStorageSync('kw', JSON.stringify(this.historyList))
			  },
			  cleanHistory() {
			    // 清空 data 中保存的搜索历史
			    this.historyList = []
			    // 清空本地存储中记录的搜索历史
			    uni.setStorageSync('kw', '[]')
			  },
		}
	}
</script>

<style lang="scss">
	.uni-searchbar {
		/* #ifndef APP-NVUE */
		display: flex;
		/* #endif */
		flex-direction: row;
		position: relative;
		padding: 16rpx;
		/* 将默认的 #FFFFFF 改为 #C00000 */
		background-color: #c00000;
	}

	.search-box {
		position: sticky;
		top: 0;
		z-index: 999;
	}
	.sugg-list {
	  padding: 0 5px;
	
	  .sugg-item {
	    font-size: 12px;
	    padding: 13px 0;
	    border-bottom: 1px solid #efefef;
	    display: flex;
	    align-items: center;
	    justify-content: space-between;
	
	    .goods-name {
	      // 文字不允许换行（单行文本）
	      white-space: nowrap;
	      // 溢出部分隐藏
	      overflow: hidden;
	      // 文本溢出后，使用 ... 代替
	      text-overflow: ellipsis;
	      margin-right: 3px;
	    }
	  }
	}
	.history-box {
	  padding: 0 5px;
	
	  .history-title {
	    display: flex;
	    justify-content: space-between;
	    align-items: center;
	    height: 40px;
	    font-size: 13px;
	    border-bottom: 1px solid #efefef;
	  }
	
	  .history-list {
		  margin-top: 10rpx;
	    display: flex;
	    flex-wrap: wrap;
	
	    .uni-tag {
	      margin-top: 5px;
	      margin-right: 5px;
	    }
	  }
	}
</style>
